
@import url('https://fonts.googleapis.com/css2?family=Reddit+Mono:wght@200..900&display=swap');

*{
  font-family: "Reddit Mono", monospace;
}
.container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
}

h1 {
  margin-bottom: 20px;
}

button {
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

.cars-list {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.car-item {
  background-color: #f4f4f4;
  padding: 10px;
  margin-bottom: 5px;
  border-radius: 5px;
}
.signal {
  align-items: center;
  justify-content: center;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  font-weight: bold;
  color: white;
  margin-top: 20px;
}

.signal-container {
  display: flex;
  justify-content: center;
  margin-top: 20px; 
}
.mute-container {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

.mute-button {
  font-size: 14px;
  padding: 5px 10px;
}

